.setting__price {
  padding-top: 20px;
  span {
    margin-bottom: 10px;
  }
  .setting__price_slider {
    margin-bottom: 50px;
    div {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 11px;
      line-height: 12px;
      color: var(--grey);
    }
  }
  .setting__price_title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      line-height: 18px;
    }
    strong {
      font-size: 36px;
      line-height: 42px;
      font-weight: 500;
    }
  }
  .setting__price_rub {
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      color: rgba(38, 38, 38, 1);
      width: 156px;
      line-height: 18px;
    }
    input {
      font-size: 16px;
      padding: 21px 24px;
      width: 250px;
      height: 65px;
      border-radius: 6px;
      border: 1px solid rgba(225, 225, 225, 1);
    }
    ::-webkit-input-placeholder {
      font-size: 16px;
    }
  }
  :nth-child(4) {
    div {
      p:nth-child(1) {
        margin-right: -30px;
      }
    }
  }
}
.fillForm {
  background-color: white;
  padding: 40px 30px;
  border-radius: 25px;
  h2 {
    font-size: 30px;
    line-height: 35px;
    span {
      color: var(--red);
    }
  }
  input {
    font-size: 16px;
    line-height: 18px;
    padding: 18px 24px;
    width: 100%;
    height: 65px;
    border-radius: 6px;
    border: 1px solid rgba(225, 225, 225, 1);
    margin-bottom: 10px;
  }
  ::-webkit-input-placeholder {
    font-size: 16px;
  }
  button {
    width: 100%;
    height: 65px;
    line-height: 18px;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 20px;
  }
  p {
    text-align: center;
    font-size: 13px;
    line-height: 16px;
    color: var(--grey);
  }
}
.setting__view {
  grid-column: 1/2;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  img {
    width: 100%;
    height: 100%;
    border-radius: 25px;
    margin-bottom: 15px;
    object-fit: contain;
    object-position: top;
  }
  .setting__view__container {
    width: 390px;
    margin: 0 auto;
    :nth-child(1) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      p {
        border-radius: 30px;
        display: block;
        min-width: 78px;
        line-height: 18px;
        padding: 5px 15px;
        background-color: rgba(173, 173, 176, 1);
        color: white;
        font-weight: bold;
        text-align: center;
      }
    }
    hr {
      background-color: rgba(225, 225, 225, 1);
      height: 6px;
      border: none;
      border-radius: 6px;
      margin: 20px 0 15px 0;
    }
    :nth-child(3) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      p {
        color: rgba(173, 173, 176, 1);
        line-height: 18px;
        width: 87px;
      }
      :nth-child(2) {
        text-align: end;
      }
    }
    .active_credit {
      p {
        background-color: var(--red);
      }
    }
    .active_hr {
      background-color: var(--red);
    }
    .active_sum {
      p {
        color: rgb(0, 0, 0);
      }
    }
  }
}
.setting__view-img {
  width: 424px;
  height: 250px;
}

@media (max-width: 978px) {
  .setting__price {
    .setting__price_title {
      p,
      strong {
        font-size: 14px;
        line-height: 16px;
      }
    }
    .setting__price_rub {
      p {
        font-size: 14px;
        line-height: 16px;
      }
      input {
        width: 163px;
      }
    }
    .setting__price_slider {
      margin-bottom: 30px;
    }
  }
  .fillForm {
    padding: 0;
    background: none;
    h2 {
      margin-bottom: 10px;
      font-size: 20px;
      line-height: 23px;
    }
    form {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
      input,
      button {
        margin: 0;
      }
      input {
        margin-right: 10px;
      }
    }
    p {
      text-align: start;
      font-size: 10px;
      line-height: 13px;
    }
  }
  .setting__view {
    width: 250px;
    img {
      width: 100%;
    }
    .setting__view__container {
      width: 220px;
      :nth-child(1) {
        p {
          font-size: 14px;
        }
        margin-bottom: 3px;
      }
      :nth-child(3) {
        font-size: 12px;
        line-height: 14px;
        p {
          width: 69px;
        }
      }
      .active_credit {
        p {
          font-size: 11px;
        }
      }
    }
  }
  .setting__view-img {
    width: 100%;
    height: 180px;
  }
}
@media (max-width: 528px) {
  .setting__price {
    padding: 0;
    .setting__price_rub {
      p {
        width: auto;
      }
    }
    .setting__price_slider {
      span {
        margin-bottom: 0px;
      }
    }
  }
  .fillForm {
    h2 {
      .br {
        display: block;
      }
    }
    form {
      display: block;
      input {
        margin-bottom: 10px;
      }
    }
  }
  .setting__view {
    width: 100%;
    .setting__view__container {
      width: 100%;
    }
  }
}
